import { SvgUri } from "react-native-svg";
import { StatusBar } from "expo-status-bar";
import { StyleSheet, Text, View, Image } from "react-native";
import React, { useState, useEffect } from "react";
import axios from "axios";

export default function App() {
  const [resp, setresp] = useState("No response from server");
  const [extern, setextern] = useState("No response from server");

  useEffect(() => {
    axios
      .get("http://api.localhost:4000/api/sample/")
      .then((res) => {
        setresp(res.data);
        console.log(res.data);
      })
      .catch((err) => {
        alert(err);
        console.log(err);
      });

    axios
      .get("https://api.spacexdata.com/v3/launches/latest?pretty=true")
      .then((res) => {
        setextern(res.data);
        console.log(res.data);
      })
      .catch((err) => {
        alert(err);
        console.log(err);
      });
  }, []);
  useEffect(() => {
    return () => {};
  }, [resp]);

  return (
    <View style={styles.container}>
      <Text>Test App</Text>
      <StatusBar style='auto' />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
